}
.m-list__card-details dd {
  margin-left: 0;
}
.m-list__card-details dd + dd {
  margin-top: 0.9375rem;
}
.m-list__card-details dd::after {
  content: none;
}
.m-list__card-details dd .o-table {
  margin-top: 0.625rem;
}

.m-apr-disclaimer {
  text-indent: -0.7ch;
}

.m-payment-calculation {
  display: flex;
  flex-direction: column;
}
@media only all and (min-width: 37.5625em) {
  .m-payment-calculation {
    flex-direction: row;
    gap: 1.875rem;
  }
}
@media only all and (min-width: 37.5625em) {
  .m-payment-calculation__operator {
    align-self: center;
    margin-bottom: 0;
  }
}

.htmx-progress {
  display: none;
  position: fixed;
  top: 0;
  z-index: 1000;
  height: 0.3125rem;
  width: 100%;
}
.htmx-progress .htmx-progress-bar {
  background-color: var(--pacific);
}
.htmx-progress .htmx-progress-bar::before, .htmx-progress .htmx-progress-bar::after {
  content: "";
  position: absolute;
  background-color: inherit;
  top: 0;
  left: 0;
  bottom: 0;
  will-change: left, right;
  animation: indeterminate 2.5s cubic-bezier(0.65, 0.815, 0.735, 0.395) infinite;
}
.htmx-progress .htmx-progress-bar::after {
  animation-name: indeterminate-short;
  animation-delay: 1s;
}

.htmx-request .htmx-progress {
  display: inline;
}

.htmx-request #htmx-results {
  opacity: 0.5;
}

html.js form[hx-boost] .o-form__group:last-child {
  margin-bottom: 0;
}
html.js form[hx-boost] .m-btn-group {
  display: none;
}

@keyframes indeterminate {
  0% {
    left: -35%;
    right: 100%;
  }
  40% {
    left: 100%;
    right: -90%;
  }
  100% {
    left: 100%;
    right: -90%;
  }
}
@keyframes indeterminate-short {
  0% {
    left: -200%;
    right: 100%;
  }
  40% {
    left: 107%;
    right: -8%;
  }
  100% {
    left: 107%;
    right: -8%;
  }
}
.a-select .a-select--error {
  outline: 2px solid var(--red);
}
.a-select .a-select--error:focus {
  box-shadow: 0 0 0 2px var(--red);
  outline: 1px dotted var(--red);
}

.a-form-alert__text {
  display: block;
  margin-left: 1.25em;
}

.u-cap-width {
  max-width: 43.75em;
}